// vmvuex
module.exports = (vm) => {
	// 
	uni.$u.http.setConfig((config) => {
		/* config */
		config.baseURL = 'https://api.shop.eduwork.cn'; /*  */
		return config
	})

	// 
	uni.$u.http.interceptors.request.use((config) => { // async await 
		// dataundefined{}
		config.data = config.data || {}
		// customtoken
		if (config?.custom?.auth) {
			// vmvuexvm.$store.state
			config.header.Authorization = "Bearer" + vm.$store.state.access_token
		}
		return config
	}, config => { // async await 
		return Promise.reject(config)
	})

	// 
	uni.$u.http.interceptors.response.use((response) => {
		/*  async await */
		// const data = response.data
		return response.data
	}, (response) => {
		//  statusCode !== 200
		const {
			statusCode,
			data
		} = response
		if (statusCode < 400) {
			return data
		} else if (statusCode == 400) {
			vm.$u.toast(data.message)
			return Promise.reject(response)
		} else if (statusCode == 401) {
			//token 
			vm.$u.toast('')
			setTimeout(() => {
				vm.$u.route('pages/login/login')
			}, 1500)
			return Promise.reject(response)
		} else if (statusCode == 422) {
			const {
				errors
			} = data
			vm.$u.toast(Object.values(errors)[0][0])
			return Promise.reject(response)
		} else {
			return Promise.reject(response)
		}
	})

	//patch
	uni.$u.http.patch = (url, params = {}, header = {}) => {
		//patch 
		const _params = {
			...params,
			_method: 'PATCH'
		}
		return uni.$u.http.post(url, _params, header)
	}
}
